	module.exports = {"pathContext":{"docHtml":"<h1>1.1.3 / 2016-02-27</h1>\n<ul>\n<li>[Fix] ensure “class “ doesn’t screw up “class” detection</li>\n<li>[Tests] up to <code>node</code> <code>v5.7</code>, <code>v4.3</code></li>\n<li>[Dev Deps] update to <code>eslint</code> v2, <code>@ljharb/eslint-config</code>, <code>jscs</code></li>\n</ul>\n<h1>1.1.2 / 2016-01-15</h1>\n<ul>\n<li>[Fix] Make sure comments don’t screw up “class” detection (#4)</li>\n<li>[Tests] up to <code>node</code> <code>v5.3</code></li>\n<li>[Tests] Add <code>parallelshell</code>, run both <code>--es-staging</code> and stock tests at once</li>\n<li>[Dev Deps] update <code>tape</code>, <code>jscs</code>, <code>nsp</code>, <code>eslint</code>, <code>@ljharb/eslint-config</code></li>\n<li>[Refactor] convert <code>isNonES6ClassFn</code> into <code>isES6ClassFn</code></li>\n</ul>\n<h1>1.1.1 / 2015-11-30</h1>\n<ul>\n<li>[Fix] do not throw when a non-function has a function in its <a href=\"#2\">[Prototype]</a></li>\n<li>[Dev Deps] update <code>tape</code>, <code>eslint</code>, <code>@ljharb/eslint-config</code>, <code>jscs</code>, <code>nsp</code>, <code>semver</code></li>\n<li>[Tests] up to <code>node</code> <code>v5.1</code></li>\n<li>[Tests] no longer allow node 0.8 to fail.</li>\n<li>[Tests] fix npm upgrades in older nodes</li>\n</ul>\n<h1>1.1.0 / 2015-10-02</h1>\n<ul>\n<li>[Fix] Some browsers report TypedArray constructors as <code>typeof object</code></li>\n<li>[New] return false for \"class\" constructors, when possible.</li>\n<li>[Tests] up to <code>io.js</code> <code>v3.3</code>, <code>node</code> <code>v4.1</code></li>\n<li>[Dev Deps] update <code>eslint</code>, <code>editorconfig-tools</code>, <code>nsp</code>, <code>tape</code>, <code>semver</code>, <code>jscs</code>, <code>covert</code>, <code>make-arrow-function</code></li>\n<li>[Docs] Switch from vb.teelaun.ch to versionbadg.es for the npm version badge SVG</li>\n</ul>\n<h1>1.0.4 / 2015-01-30</h1>\n<ul>\n<li>If @@toStringTag is not present, use the old-school Object#toString test.</li>\n</ul>\n<h1>1.0.3 / 2015-01-29</h1>\n<ul>\n<li>Add tests to ensure arrow functions are callable.</li>\n<li>Refactor to aid optimization of non-try/catch code.</li>\n</ul>\n<h1>1.0.2 / 2015-01-29</h1>\n<ul>\n<li>Fix broken package.json</li>\n</ul>\n<h1>1.0.1 / 2015-01-29</h1>\n<ul>\n<li>Add early exit for typeof not \"function\"</li>\n</ul>\n<h1>1.0.0 / 2015-01-29</h1>\n<ul>\n<li>Initial release.</li>\n</ul>","docPath":"argo-ci/node_modules/is-callable/changelog","proj":"argo-ci"}}

